
@use 'variables' as v;
@use 'colores' as c;
@use 'tipografia' as t;
@use 'mixins' as m;


// Botones
@mixin boton($bgColor, $colorTexto, $display) {
    background-color: $bgColor;
    display: $display;

    color: $colorTexto;
    font-size: 1.2rem;
    text-align: center;
    
    margin: 0.2rem 0.2rem 0.2rem 0;
    padding: 1rem 2rem;

    border: none;
    border-radius: 0.5rem;

    transition-property: background-color;
    transition-duration: .3s;
    
    width: 100%;

    @include m.tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}


