
// Botones

@use 'base/variables' as v;
@use 'base/botonesmixins' as b;
@use 'base/colores' as c;
@use 'base/mixins' as m;



.boton_verde{
    @include b.boton(c.$green-A700, c.$blanco, block);
}

.boton_rojo {
    @include b.boton(c.$rojo, c.$blanco, inline-block);
}

.pulsacion2{
    cursor: pointer;
}

.pulsacion2:active {
    transform: scale(0.80);
}

.boton_verde:active {
    transform: scale(0.90);
    box-shadow: 2px 2px 10px 1px rgba(0, 1, 14, 0.24);
}

.botonCerrar:active {
    transform: scale(0.90);
    box-shadow: 2px 2px 10px 1px rgba(0, 1, 14, 0.24);
}


