@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.meteo {

    hr {
        border: 1px solid lighten(c.$blue-grey-200, 10%);
    }

    .conte_uno,
    .conte_dos,
    .conte_tres {
        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 0.5rem;
    }

    .conte_uno {

        .parra {
            display: flex;
        }
    }

    .conte_dos {

        .izquierda,
        .derecha {
            img {
                max-width: 2rem;
            }

            .contenido {
                display: flex;
            }
        }

        .centro {
            img {
                max-width: 10rem;
            }
        }
        .uno,
        .dos,
        .tres {
            display: flex;
        }
    }

    .conte_tres {

        .viento {
            .contenido {
                display: flex;
            }

            p {
                font-size: 1rem;
            }
        }
    }

    .conte_cuatro {
        display: grid;
        grid-template-columns: repeat(2, 50%);
        gap: 0.5rem;
  
        margin-top: 0.5rem;
        margin-right: 0.5rem;

        .conte_dia0,
        .conte_dia1,
        .conte_dia2,
        .conte_dia3 {
            border: 1px solid lighten(c.$blue-grey-200, 10%);
            border-radius: 0.5rem;
            padding: 0.5rem;

            .titulo {
                display: flex;
                justify-content: center;
            }

            .columnas {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .contenido {
                    display: flex;
                }
                p {
                    font-size: 1.1rem;
                }
            }

            
        }

    }
}