h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 1.5rem 0;
  font-weight: 900;
  color: rgb(33, 192, 192);
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  margin: 0;
}

h4 {
  font-size: 2.5rem;
  color: rgb(33, 192, 192);
  margin: 0;
  padding: 0;
}

h5 {
  font-size: 1.2rem;
  font-family: "Poppins", sans-serif;
  color: rgb(33, 192, 192);
  margin: 0;
  padding: 0;
}

p {
  color: #1a1b15;
  font-size: 1.2rem;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  background-color: #FFFFFF;
  color: rgb(33, 192, 192);
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  min-height: 100vh;
  margin: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

footer {
  bottom: 0;
  width: 100%;
}

/** Media Queries **/
.mostrar {
  display: block !important;
}

.ocultar {
  display: none !important;
}

.sombraCaja {
  -webkit-box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
  -moz-box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
  box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
}

.sombraTarjetas {
  box-shadow: 0px 0px 10px -2px rgba(148, 139, 139, 0.76);
  -webkit-box-shadow: 0px 0px 10px -2px rgba(148, 139, 139, 0.76);
  -moz-box-shadow: 0px 0px 10px -2px rgba(148, 139, 139, 0.76);
}

.swal2-popup.swal2-toast {
  box-sizing: border-box;
  grid-column: 1/4 !important;
  grid-row: 1/4 !important;
  grid-template-columns: min-content auto min-content;
  padding: 1em;
  overflow-y: hidden;
  background: #fff;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.075), 0 1px 2px rgba(0, 0, 0, 0.075), 1px 2px 4px rgba(0, 0, 0, 0.075), 1px 3px 8px rgba(0, 0, 0, 0.075), 2px 4px 16px rgba(0, 0, 0, 0.075);
  pointer-events: all;
}

.swal2-popup.swal2-toast > * {
  grid-column: 2;
}

.swal2-popup.swal2-toast .swal2-title {
  margin: 0.5em 1em;
  padding: 0;
  font-size: 1em;
  text-align: initial;
}

.swal2-popup.swal2-toast .swal2-loading {
  justify-content: center;
}

.swal2-popup.swal2-toast .swal2-input {
  height: 2em;
  margin: 0.5em;
  font-size: 1em;
}

.swal2-popup.swal2-toast .swal2-validation-message {
  font-size: 1em;
}

.swal2-popup.swal2-toast .swal2-footer {
  margin: 0.5em 0 0;
  padding: 0.5em 0 0;
  font-size: 0.8em;
}

.swal2-popup.swal2-toast .swal2-close {
  grid-column: 3/3;
  grid-row: 1/99;
  align-self: center;
  width: 0.8em;
  height: 0.8em;
  margin: 0;
  font-size: 2em;
}

.swal2-popup.swal2-toast .swal2-html-container {
  margin: 0.5em 1em;
  padding: 0;
  overflow: initial;
  font-size: 1em;
  text-align: initial;
}

.swal2-popup.swal2-toast .swal2-html-container:empty {
  padding: 0;
}

.swal2-popup.swal2-toast .swal2-loader {
  grid-column: 1;
  grid-row: 1/99;
  align-self: center;
  width: 2em;
  height: 2em;
  margin: 0.25em;
}

.swal2-popup.swal2-toast .swal2-icon {
  grid-column: 1;
  grid-row: 1/99;
  align-self: center;
  width: 2em;
  min-width: 2em;
  height: 2em;
  margin: 0 0.5em 0 0;
}

.swal2-popup.swal2-toast .swal2-icon .swal2-icon-content {
  display: flex;
  align-items: center;
  font-size: 1.8em;
  font-weight: bold;
}

.swal2-popup.swal2-toast .swal2-icon.swal2-success .swal2-success-ring {
  width: 2em;
  height: 2em;
