@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

.domo {

    .tarjetas {
        display: flex;
        
        .bat_24,
        .bat_48,
        .temp,
        .hume,
        .agua,
        .solar {
            display: block;
            border: 1px solid lighten(c.$blue-grey-200, 10%);
            border-radius: 0.5rem;

            width: 50%;

            margin: 0.5rem;
            padding: 0.5rem;
        }


        .bat_24,
        .bat_48,
        .agua,
        .solar {

            .conte {
            display: flex;
            justify-content: center;
            align-items: center;

            padding: 0.5rem;
            margin-top: 0.5rem;
            }

            img {
                max-width: 3rem;
            }
        }

        .temp,
        .hume {

            .conte {
                display: flex;

                padding: 0.5rem;
                margin-top: 0.5rem;

                .cont {
                    display: block;
                    text-align: center;

                    .parrafo {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                }
            }

            img {
                max-width: 4rem;
            }
        }
        .agua, 
        .solar {
            margin-bottom: 1rem;
            
            .parrafo {
                display: flex;
                justify-content: center;
                align-items: center;
            }
        
            img {
                max-width: 4rem;
            }
        }


        .tit {
            text-align: center;
        }

        p {
            padding: 0.5rem;
        }
    }

    .puerta {
        margin-top: 2rem;
    }
    .puerta,
    .mando,
    .motor,
    .ilu,
    .meteo {
        margin-bottom: 0.5rem;
    }

    .contenedor_puerta,
    .contenedor_mando,
    .contenedor_motor,
    .contenedor_ilu,
    .contenedor_meteo {
        display: block;
        border: 1px solid lighten(c.$blue-grey-200, 10%);
        border-radius: 0.5rem;
        
        margin: 1rem 0;
        padding: 0.5rem;
    }



    .btn_desplegable {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;

        .img {
            margin-right: 1rem;
            max-width: 3rem;
        }
    }

    .desple {
        position: absolute;
        z-index: 10;
        visibility: hidden;
        opacity: 0;

        margin: 0;
        padding: 0rem;

        width: 95%;

        background-color: c.$blanco;
        
        border: 1px solid darken(c.$gris, 10%);
        border-radius: 1rem;

        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);

        max-height: 25rem;
        overflow-y: auto;
        overscroll-behavior: auto;

    }

    .abrir{
        visibility: visible;
        opacity: 1;
        transition: visibility 0s linear 0s, opacity 0.5s;  
    }

    .puerta {

        .contenedor_puerta {
            margin-top: 0;

            .abrir_puerta {
                display: flex;
            }

            .foco {
                margin-top: 1rem;

                .botones_foco {
                    display: flex;
                }
            }
        }
    
    }

    .mando {
        .botones_inversor,
        .botones_termo {
            display: flex;
        }
    }

    .motor{
        
        img {
            max-width: 5rem;
        }

        .contenedor_arriba{
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .contenedor_boton {
            width: 100%;
            padding: 1rem;
        }

        .contenedor_contadores{
            text-align: center;

            .contadores {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .totalHoras,
                .totalMes,
                .totalAhora {
                    text-align: center;
                }
            }
        }



    }

    .ilu {
        
        .contenedor_arriba{
            display: flex;
            justify-content: space-between;
            align-items: center;

            img {
                max-width: 5rem;
            }
        }
        .contenedor_botones {
            display: flex;
        }
    }
}